html, body {
  min-width: 480px; }

body {
  background-color: #c5c1c0;
  color: #0a1612; }

.App {
  text-align: center;
  margin: 0 auto;
  width: 100vw;
  height: 173vw;
  position: relative; }

@media (min-aspect-ratio: 9 / 16) {
  .App {
    height: 96vh;
    width: 55.68vh; } }

.panel {
  height: 67%;
  margin: 0;
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.panel_main {
  width: 67%; }

.panel_aside {
  width: 33%; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 0.3s linear;
          animation: App-logo-spin infinite 0.3s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(80deg);
            transform: rotate(80deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(80deg);
            transform: rotate(80deg); } }

.score {
  margin-top: 0; }

h1 {
  text-align: center;
  text-transform: uppercase; }



