.btn {
  background-color: #f7c331;
  color: #1a2930;
  display: block;
  font-size: 1.25rem;
  padding: 0.5em;
  font-weight: bold; }

@-webkit-keyframes pulse {
  from {
    background-color: #f7c331; }
  50% {
    background-color: #fdeec4; }
  to {
    background-color: #f7c331; } }

@keyframes pulse {
  from {
    background-color: #f7c331; }
  50% {
    background-color: #fdeec4; }
  to {
    background-color: #f7c331; } }

.press_me {
  -webkit-animation: pulse 1s linear infinite;
          animation: pulse 1s linear infinite; }



